.App-header {
  background-color:rgb(21, 10, 43);
  padding: 20px; 
  color: white;
  width: 100%;
  position: sticky;
  top:0;
  height: 120px
}

.App-title {
  font-size: 3em;
  font-family: 'SEGA LOGO FONT';
