}



@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@media screen and (max-width: 30em) {
  .p{
        padding-top: 1rem;
  }

  .App-header {
  
  height: 100px;
  padding: 20px;
  color: white;
}
.App-logo {
  /*animation: App-logo-spin infinite 20s linear;*/
  height: 170px;
  width: 65%;
}
}
@media screen and (max-width: 60em) and (min-width: 30em) {
  
 .App-header {
  height: 100px;
  padding: 20px;
  color: white;
}
.App-logo {
  /*animation: App-logo-spin infinite 20s linear;*/
