.button {
  background-color: transparent; /* Green */
  border: 0.5px solid white;
  border-radius: 150px;
  color: white;
  width: 150px;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px; /* Safari */
  transition-duration: 0.4s;
  cursor: pointer;
}
.button:hover {
  background-color: #2b96ce;
  color: white;
}
